.search {
  position: fixed;
  top: 0;
  left: -120%;
  display: block;
  padding: 40px;
  width: 30%;
  height: 100%;
  background-color: #fff;
  z-index: 1000;
  box-shadow: 8px 0 28px rgba(0, 0, 0, 0.3);
  visibility: hidden;
  pointer-events: none;
  opacity: 0;
  transition: all .8s;
  &.active {
	left: 0;
	visibility: visible;
	pointer-events: visible;
	opacity: 1;
	transition: all .8s;
  }
  &-wrap {
	display: flex;
	align-items: center;
	input {
	  padding: 0;
	  width: 80%;
	  height: 40px;
	  color: black;
	  border: 1px solid #ccc;
	}
	button {
	  display: flex;
	  justify-content: center;
	  align-items: center;
	  padding: 0;
	  width: 20%;
	  height: 42px;
	  color: white;
	  background-color: #0d3b66;
	  border: 1px solid #0d3b66;
	}
  }
  &__list {
	list-style-type: none;
	&-item {
	  list-style-type: none;
	  border-bottom: 1px solid #ccc;
	}
	&-link {
	  text-decoration: none;
	}
  }
}
